import { Injectable } from '@angular/core';
import { User } from '../models/user';
import { HttpClient, HttpHeaders } from "@angular/common/http";
@Injectable({
  providedIn: 'root'
})
export class UserService {
  private stateList = "./assets/data/india_states_list.json";
  private timeZoneList = "./assets/data/times_zone.json";
  private upersons: User[] = [];

  constructor(private http: HttpClient) { }

  getUsersFromData(): User[] {
    return this.upersons;
  }

  addUser(user: User) {
    user.id = this.upersons.length + 1;
    this.upersons.push(user);
  }

  updateUser(user: User) {
    const index = this.upersons.findIndex(u => user.id === u.id);
    this.upersons[index] = user;
  }
  deleteUser(user: User) {
    this.upersons.splice(this.upersons.indexOf(user), 1);
  }

  getStateList() {
    return this.http.get(this.stateList);
  }

  getTimeZoneList() {
    return this.http.get(this.timeZoneList);
  }

}
