import { Component, OnInit, Inject } from '@angular/core';
import { User } from '../../models/user';
import { UserService } from '../../services/user.service';
import { ActivatedRoute, Router } from '@angular/router';
import { MatSnackBar } from "@angular/material/snack-bar";
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {
	FormControl,
	FormGroup,
	FormBuilder,
	Validators
} from "@angular/forms";
import { DialogContentComponent } from './dialogContent/dialog-content.component';

const usPattern = '^[7-9][0-9]{9}$';

@Component({
  selector: 'app-location-add',
  templateUrl: './location-add.component.html',
  styleUrls: ['./location-add.component.scss']
})



export class LocationAddComponent implements OnInit {
  locationForm: FormGroup;
  stateList: any = [];
  timesZone: any = [];
  userId = '';
  appointmentList = [
      {id: 1, name: 'pool 1'},
      {id: 2, name: 'pool 2'},
      {id: 4, name: 'pool 3'},
      {id: 5, name: 'pool 4'}
  ];


  task: any = {
    name: 'ALL tasks',
    completed: false,
    color: 'primary',
    subtasks: [
      {name: '10:30', completed: false, color: 'primary'},
      {name: '10:30', completed: false, color: 'primary'},
      {name: '10:30', completed: false, color: 'primary'},
      {name: '10:30', completed: false, color: 'primary'},
      {name: '10:30', completed: false, color: 'primary'},
      {name: '10:30', completed: false, color: 'primary'}
    ]
  };

  allComplete: boolean = false;

  updateAllComplete() {
    this.allComplete = this.task.subtasks != null && this.task.subtasks.every(t => t.completed);
  }

  someComplete(): boolean {
    if (this.task.subtasks == null) {
      return false;
    }
    return this.task.subtasks.filter(t => t.completed).length > 0 && !this.allComplete;
  }

  setAll(completed: boolean) {
    console.log(completed);
    this.allComplete = completed;
    if (this.task.subtasks == null) {
      return;
    }
    this.task.subtasks.forEach(t => t.completed = completed);
  }


  constructor(private userService: UserService, public snackBar: MatSnackBar, private fb: FormBuilder, private router: Router, public dialog: MatDialog, private activetedRoute: ActivatedRoute) {
    
   }

   openDialog() {
    const dialogRef = this.dialog.open(DialogContentComponent);

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  

  ngOnInit(): void {
    this.activetedRoute.paramMap.subscribe((params: any) => {
      this.userId = params.id;
      console.log('params id', this.userId);
    });
    this.createForm();
    this.getStateData();
    this.getTimeZoneData();
    
  }

  getStateData(){
    this.userService.getStateList().subscribe((res: any) => {
      this.stateList = res.states;
    }, err => {
      this.snackBar.open('Error occured', 'Error', {
        duration: 2000
      })
    })
  }

  getTimeZoneData(){
    this.userService.getTimeZoneList().subscribe((res: any) => {
      this.timesZone = res;
    }, err => {
      this.snackBar.open('Error occured', 'Error', {
        duration: 2000
      })
    })
  }


  private createForm() {
		this.locationForm = this.fb.group({
			// locationName: ['', Validators.required],
			// addressLine1: ['', Validators.required],
			// addressLine2: ['', Validators.required],
			// suite: ['', Validators.required],
			// city: ['', Validators.required],
			// state: ['', Validators.required],
			// zip: ['', Validators.required],
			// phone: ['', Validators.required],
			// timezone: ['', Validators.required],
			// facility: [],
      // appointment: ['', Validators.required]
      locationName: [''],
			addressLine1: [''],
			addressLine2: [''],
			suite: [''],
			city: [''],
			state: [''],
			zip: [''],
			phone: ['', [Validators.required, Validators.pattern(usPattern)]],
			timezone: [''],
			facility: [],
			appointment: ['']
		});
	}

	getFormControl(name: any) {
		return this.locationForm.get(name);
  }
  
  cancelNewUser(){
    this.router.navigate(['/']);
  }
 

  onSubmit(){
    if(this.locationForm.value){
      this.userService.addUser(this.locationForm.value);
      this.snackBar.open('Location added successfully', 'Success', {
        duration: 2000
      })
      this.router.navigate(['/']);
    }
    
  }

}


