import { Component, OnInit, Inject } from '@angular/core';
import {MatSnackBar} from '@angular/material/snack-bar';
import {ThemePalette} from '@angular/material/core';

import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ComponentPortal } from '@angular/cdk/portal';
import {
	FormControl,
	FormGroup,
	FormBuilder,
  Validators,
  FormArray
} from "@angular/forms";

@Component({
  selector: 'app-dialog-content',
  templateUrl: './dialog-content.component.html',
  styleUrls: ['./dialog-content.component.scss']
})
export class DialogContentComponent implements OnInit {
  portal: ComponentPortal<any>;
  setTimeForm: FormGroup;
  task: any = {
    name: 'ALL tasks',
    from: '',
    to:'',
    completed: false,
    color: 'primary',
    subtasks: [
      {day: 'Sun', from:'',to:'', completed: false, color: 'primary'},
      {day: 'Mon', from:'',to:'', completed: false, color: 'primary'},
      {day: 'Tue', from:'',to:'', completed: false, color: 'primary'},
      {day: 'Wed', from:'',to:'', completed: false, color: 'primary'},
      {day: 'Thu', from:'',to:'', completed: false, color: 'primary'},
      {day: 'Fri', from:'',to:'', completed: false, color: 'primary'},
      {day: 'Sat', from:'',to:'', completed: false, color: 'primary'}
      
    ]
  };


  allComplete: boolean = false;
  pushedfrom: number;
  pushedto: number;


  constructor( public dialogRef: MatDialogRef<DialogContentComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, private fb: FormBuilder) { }

  ngOnInit(): void {
   // this.createForm();

    this.setTimeForm = this.fb.group({
      fromTime: [],
      ToTime: [],
      checkArray: this.fb.array([])
    })
  }
  

  onCheckboxChange(e) {
    const checkArray: FormArray = this.setTimeForm.get('checkArray') as FormArray;
  
    if (e.target.checked) {
      checkArray.push(new FormControl(e.target.value));
    } else {
      let i: number = 0;
      checkArray.controls.forEach((item: FormControl) => {
        if (item.value == e.target.value) {
          checkArray.removeAt(i);
          return;
        }
        i++;
      });
    }
  }

  public createForm (){
    return this.setTimeForm =  this.fb.group({
      fromTime: [],
      ToTime: [],
      day: [],
    });
  }

 

  updateAllComplete() {
    this.allComplete = this.task.subtasks != null && this.task.subtasks.every(t => t.completed);

  }
  someComplete(): boolean {
    if (this.task.subtasks == null) {
      return false;
    }
    return this.task.subtasks.filter(t => t.completed).length > 0 && !this.allComplete;
  }

  setAll(completed: boolean,index:number) {

    console.log(completed);
    this.allComplete = completed;
    if (this.task.subtasks == null) {
      return;
    }

    this.task.subtasks.forEach(t => t.completed = completed);
    this.pushedfrom = this.task.subtasks[index].from;
    this.pushedto = this.task.subtasks[index].to;
    this.task.subtasks.forEach(t => {
     t.from = this.pushedfrom;
      t.to = this.pushedto;
    });
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
  onSubmit(){
    console.log(this.setTimeForm.value);
    this.dialogRef.close();
    console.log('task', this.task.subtasks);
  }

  

}
