import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LocationListComponent } from './views/location-list/location-list.component';
import { LocationAddComponent } from './views/location-add/location-add.component';

const routes: Routes = [
  { path: '', component: LocationListComponent },
  { path: 'add-location', component: LocationAddComponent },
  { path: 'add-location/:id', component: LocationAddComponent },
  { path: '**', redirectTo: '', pathMatch: 'full' }
];

@NgModule({
  declarations: [
  ],
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
